export function InvalidTrafficAnalysis() {
  const cardClass = "bg-white border-gray-200 shadow-sm";
  
  return (
    <div className="border border-gray-200 rounded-md bg-white">
      <div className="bg-gray-100 py-2 px-4 border-b border-gray-200 text-center">
        <h2 className="text-lg font-semibold">Invalid Traffic (SIVT)</h2>
      </div>

      <div className="flex flex-col">
        <div className="grid grid-cols-2 gap-1 border-b border-gray-200">
          <div className="border-r border-gray-200 py-2 px-4">
            <span className="font-semibold">Repeat User: 8%</span>
          </div>
          <div className="py-2 px-4">
            <span className="font-semibold">Suspicious Behaviour: 4%</span>
          </div>
        </div>
        
        <div className="grid grid-cols-1 lg:grid-cols-2">
          {/* Left section */}
          <div className="border-r border-gray-200">
            <div className="border-b border-gray-200">
              <Table>
                <TableHeader className="bg-gray-100">
                  <TableRow>
                    <TableHead className="text-xs font-semibold text-gray-700 p-2">Device Signature</TableHead>
                    <TableHead className="text-xs font-semibold text-gray-700 p-2">Publisher Name</TableHead>
                    <TableHead className="text-xs font-semibold text-gray-700 p-2">Sub Publisher</TableHead>
                    <TableHead className="text-xs font-semibold text-gray-700 p-2 text-center">Visit</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {deviceSignatureData.map((item, index) => (
                    <TableRow key={index} className="hover:bg-gray-50 border-b border-gray-200">
                      <TableCell className="text-xs font-mono p-2">{item.id}</TableCell>
                      <TableCell className="text-xs p-2">{item.publisher}</TableCell>
                      <TableCell className="text-xs p-2">{item.subPublisher}</TableCell>
                      <TableCell className="text-xs p-2 text-center">{item.visits}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
            
            <div className="py-2 px-4 border-b border-gray-200">
              <span className="font-semibold">IP Repeat: 7%</span>
            </div>
            
            <Card className="shadow-none border-0 rounded-none">
              <CardContent className="p-2">
                <div className="h-[240px]">
                  <ResponsiveContainer width="100%" height="100%">
                    <BarChart
                      data={repeatIpsData}
                      layout="vertical"
                      margin={{
                        top: 5,
                        right: 30,
                        left: 150,
                        bottom: 5,
                      }}
                      barSize={10}
                    >
                      <CartesianGrid strokeDasharray="3 3" horizontal={true} vertical={true} stroke="#e5e7eb" />
                      <XAxis 
                        type="number" 
                        tick={{ fontSize: 10 }} 
                        stroke="#6b7280" 
                        domain={[0, 250]}
                      />
                      <YAxis 
                        dataKey="name" 
                        type="category" 
                        tick={{ fontSize: 10 }} 
                        stroke="#6b7280" 
                        width={140}
                        tickFormatter={(value) => value.length > 20 ? `${value.substring(0, 20)}...` : value}
                      />
                      <Tooltip
                        contentStyle={{
                          backgroundColor: "#fff",
                          border: "1px solid #e5e7eb",
                          borderRadius: "4px",
                          fontSize: "11px",
                        }}
                      />
                      <Legend 
                        layout="horizontal" 
                        verticalAlign="bottom" 
                        align="center"
                      />
                      <Bar dataKey="totalEvent" name="Total Event" fill="#FF8042" />
                      <Bar dataKey="totalVisit" name="Total Visit" fill="#8884d8" />
                    </BarChart>
                  </ResponsiveContainer>
                </div>
              </CardContent>
            </Card>
          </div>
          
          {/* Right section */}
          <div>
            <div className="grid grid-cols-2 gap-x-1 border-b border-gray-200">
              <div className="p-4 flex flex-col gap-3">
                <div className="bg-yellow-100 border border-yellow-200 p-3 text-center">
                  <div className="text-sm font-semibold">Mouse Movement: FALSE</div>
                  <div className="text-xs mt-1">User has NOT interacted with the website.</div>
                </div>
                <div className="bg-yellow-100 border border-yellow-200 p-3 text-center">
                  <div className="text-sm font-semibold">Touch Support: FALSE</div>
                  <div className="text-xs mt-1">User has NOT interacted with the website.</div>
                </div>
                <div className="bg-yellow-100 border border-yellow-200 p-3 text-center">
                  <div className="text-sm font-semibold">HasFocus = FALSE</div>
                  <div className="text-xs mt-1">Page is not opening in the Active tab.</div>
                </div>
              </div>
              
              <div className="p-4">
                <div className="text-sm font-semibold mb-2">Bot Behaviour %</div>
                <div className="h-[180px] w-full flex items-center justify-center">
                  <ResponsiveContainer width="100%" height="100%">
                    <PieChart>
                      <Pie
                        data={botBehaviorData}
                        innerRadius={0}
                        outerRadius={70}
                        fill="#8884d8"
                        dataKey="value"
                        nameKey="name"
                        label={({ name, value }) => `${value}%`}
                      >
                        {botBehaviorData.map((entry, index) => (
                          <Cell key={`cell-${index}`} fill={entry.color} />
                        ))}
                      </Pie>
                      <Legend 
                        layout="horizontal" 
                        verticalAlign="bottom" 
                        align="center"
                        formatter={(value, entry) => {
                          return <span style={{ color: entry.color }}>• {value}</span>;
                        }}
                      />
                    </PieChart>
                  </ResponsiveContainer>
                </div>
              </div>
            </div>
            
            <div className="py-2 px-4 border-b border-gray-200">
              <span className="font-semibold">Server Farm: 3%</span>
            </div>
            
            <Card className="shadow-none border-0 rounded-none">
              <CardContent className="p-2">
                <div className="h-[240px]">
                  <ResponsiveContainer width="100%" height="100%">
                    <BarChart
                      data={hwConcurrencyData}
                      layout="vertical"
                      margin={{
                        top: 5,
                        right: 30,
                        left: 50,
                        bottom: 5,
                      }}
                      barSize={15}
                    >
                      <CartesianGrid strokeDasharray="3 3" horizontal={true} vertical={true} stroke="#e5e7eb" />
                      <XAxis 
                        type="number" 
                        tick={{ fontSize: 10 }} 
                        stroke="#6b7280" 
                        domain={[0, 100]}
                        tickFormatter={(value) => `${value}%`}
                      />
                      <YAxis 
                        dataKey="name" 
                        type="category" 
                        tick={{ fontSize: 10 }} 
                        stroke="#6b7280" 
                        width={50}
                      />
                      <Tooltip
                        formatter={(value) => [`${value}%`]}
                        contentStyle={{
                          backgroundColor: "#fff",
                          border: "1px solid #e5e7eb",
                          borderRadius: "4px",
                          fontSize: "11px",
                        }}
                      />
                      <Legend 
                        layout="horizontal" 
                        verticalAlign="bottom" 
                        align="center"
                      />
                      <Bar dataKey="mobile" name="Mobile" fill="#FF8042" />
                      <Bar dataKey="desktop" name="Desktop" fill="#0088FE" />
                    </BarChart>
                  </ResponsiveContainer>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  );
} 